.container{
    height: 100vh;
    width: 100vw; 
    background-color:cadetblue;
    display:flex;
    align-items: center;
    justify-content: flex-end;
}



.container form{
    width: 350px;
    height: 450px;
    border-radius: 10px;
    padding: 40px 0;
    margin-right: 25px;
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(1, 0, 0, 0.5);
}
.input{
    color: cadetblue;
    margin-bottom: 25px;
    width: 60%;
    background-color:transparent;
    padding:10px;

    outline: none;
    border: none;
    border-bottom: 1px solid cadetblue;
}
.input:hover{
    background-color: transparent;
    transition: all 1s;
    border-bottom:solid 1px rgb(255, 255, 255) ;
}
.input:focus{
    background-color: transparent;
    border-bottom:solid 1px rgb(255, 255, 255) ;
}
.input::placeholder{
    color:cadetblue;
    text-align: center;
}
.btn{
    background-color: cadetblue;
    color:white;
    margin-top: 15px;
    border: none;
    border-radius: 15px;
    padding: 10px 25px;
}
.btn:hover{
    cursor: pointer;
    background-color: #5f9ea0cc;
    color:white;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
.pass-warning{
    color: azure;
    font-size: 10px;
}
.hidden{
    display:none;
}
.status{
    position:absolute;
    margin-top: 380px;
    font-size: 13px;
    color:cadetblue ;
    
}